*,
*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
.page__body {
  position: relative;
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  background-color: #ffffff;
  color: #000000;
  margin: 0;
}

@media (min-width: 1440px) {
  .page__body--image::before {
    content: "";
    width: 50%;
    height: 694px;
    background-image: url("../img/background-index-desktop.jpg");
    position: absolute;
    top: 0;
    right: 0;
    background-size: contain;
    background-repeat: no-repeat;
    background-color: #64a83c;
    background-position-x: right;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .page__body--image::before {
    background-image: url("../img/background-index-desktop@2x.jpg");
  }
}
.header {
  width: 100%;
}

.header__wrap {
  width: 320px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 14px 20px 10px;
  min-height: 64px;
  position: relative;
  border-bottom: 1px solid #ebebeb;
  flex-wrap: wrap;
}

.header__logo-text {
  padding-right: 12px;
  margin: 0 auto;
}

.header__logo-text-img:hover,
.header__logo-img:hover {
  opacity: 0.8;
}

.header__logo-text-img:active,
.header__logo-img:active {
  opacity: 0.6;
}

@media (min-width: 768px) {
  .header__wrap {
    width: 768px;
    padding: 24px 30px 32px;
    border: none;
  }

  .header__logo-text {
    display: none;
  }

  .navigation-menu {
    width: auto;
  }
}
@media (min-width: 1440px) {
  .header__wrap {
    width: 1220px;
    padding: 53px 0 32px 0;
    border: none;
  }

  .header__logo-text {
    display: none;
  }
}
.navigation-menu__toggle {
  width: 24px;
  height: 30px;
  border: none;
  padding: 0;
  margin: 0;
  background-color: #ffffff;
  background-repeat: no-repeat;
}

.navigation-menu__list {
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  width: 100%;
  left: 0;
  right: 0;
  background-color: #ffffff;
  top: 65px;
  display: none;
}

.navigation-menu__link {
  color: #000000;
  text-decoration: none;
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  padding: 22.5px 0;
  display: block;
  text-align: center;
  border-top: 1px solid #e6e6e6;
  text-transform: uppercase;
  padding-right: 2px;
}
.navigation-menu__link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.navigation-menu__link:active {
  color: rgba(0, 0, 0, 0.3);
}

.navigation-menu__toggle-text {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@media (max-width: 767px) {
  .navigation-menu--open .navigation-menu__toggle {
    background-image: url("../img/btn-header-open.svg");
    background-position: 1px 2px;
  }

  .navigation-menu--close .navigation-menu__toggle {
    background-image: url("../img/btn-header-close.svg");
    background-position: 2px 1px;
  }

  .navigation-menu--close .navigation-menu__list {
    display: block;
  }

  .navigation-menu--no-js {
    width: 100%;
  }
  .navigation-menu--no-js .navigation-menu__toggle {
    display: none;
  }
  .navigation-menu--no-js .navigation-menu__list {
    display: block;
    position: static;
  }
}
@media (min-width: 768px) {
  .navigation-menu__toggle {
    display: none;
  }

  .navigation-menu__list {
    display: flex;
    position: static;
    background: rgba(0, 0, 0, 0);
    margin-right: 3px;
    margin-top: 6px;
  }

  .navigation-menu__item {
    margin-right: 35px;
    position: relative;
  }
  .navigation-menu__item:last-child {
    margin-right: 0;
  }
  .navigation-menu__item--active::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #68b738;
    top: 30px;
    left: 1px;
  }

  .navigation-menu__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    border: none;
    padding: 0;
  }
}
@media (min-width: 1440px) {
  .navigation-menu__link {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    color: #000000;
    text-transform: uppercase;
  }

  .navigation-menu__link--white {
    color: #ffffff;
  }
  .navigation-menu__link--white:hover {
    color: rgba(255, 255, 255, 0.6);
  }
  .navigation-menu__link--white:active {
    color: rgba(255, 255, 255, 0.3);
  }

  .navigation-menu__item {
    margin-right: 36px;
  }
  .navigation-menu__item:last-child {
    margin-right: 0;
  }

  .navigation-menu__item--active-white::after {
    background-color: #ffffff;
  }

  .navigation-menu__list {
    margin: 0;
  }
}
.food__wrap {
  width: 320px;
  margin: 0 auto;
}

.food__food-promo {
  text-align: center;
  padding: 27px 20px 0;
  background-image: url("../img/background-index.jpg");
  background-repeat: no-repeat;
}

.food__title {
  color: #ffffff;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  margin: 0;
  margin-bottom: 25px;
}

.food__lead {
  margin: 0;
  margin-bottom: 29px;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
