.food__item {
  padding: 0 33px 0 20px;
  background-color: #f2f2f2;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  position: relative;
}
.food__item--slim::after {
  content: "";
  position: absolute;
  width: 50px;
  height: 53px;
  background-image: url("../img/cat-black-mobile.svg");
  background-repeat: no-repeat;
  left: 22px;
  top: 21px;
}
.food__item--pro::after {
  content: "";
  position: absolute;
  width: 67px;
  height: 50px;
  background-image: url("../img/cat-black2-mobile.svg");
