}

.food__item-text {
  margin: 0;
  padding-bottom: 23px;
  color: #444444;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  border-bottom: 1px solid #d9d9d9;
}

.food__button {
  height: 40px;
  color: #ffffff;
  background-color: #68b738;
  line-height: 18px;
  text-decoration: none;
  text-transform: uppercase;
