  justify-content: center;
  align-items: center;
  width: 100%;
  font-size: 14px;
}
.food__button:hover {
  background-color: #5eaa2f;
}
.food__button:active {
  color: rgba(255, 255, 255, 0.3);
}

.food__item-link {
  height: 40px;
  color: #ffffff;
  background-color: #68b738;
  line-height: 18px;
  text-decoration: none;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #000000;
  background-color: #f2f2f2;
  width: 124px;
  height: 24px;
  font-size: 16px;
  line-height: 16px;
  padding: 4px 4px 5px 0;
  display: flex;
  justify-content: start;
  align-self: start;
  margin-top: 15px;
  margin-bottom: 18px;
  position: relative;
}
.food__item-link:hover .food__pro-vector,
.food__item-link:hover .food__slim-vector {
  display: none;
}
.food__item-link:hover .food__slim-vector-long,
.food__item-link:hover .food__pro-vector-long {
  display: initial;
}
.food__item-link:active {
  opacity: 0.3;
}
.food__item-link:active .food__slim-vector-long,
.food__item-link:active .food__pro-vector-long {
