}

.food__slim-vector {
  position: absolute;
  left: 102px;
  top: 7px;
}

.food__pro-vector {
  position: absolute;
  left: 100px;
  top: 5px;
}

.food__slim-vector-long {
  position: absolute;
  left: 102px;
  top: 7px;
  display: none;
}

.food__pro-vector-long {
  position: absolute;
  left: 100px;
  top: 5px;
