    content: "";
    background-color: #68b738;
    height: 2px;
    width: 70px;
    top: 22px;
    left: -111px;
  }
}
@media (min-width: 1440px) {
  .page-wrapper {
    width: 1440px;
    margin: 0 auto;
    display: flex;
    padding: 0 110px 86px;
    gap: 80px;
  }
}